import { SocialSharing } from '@ionic-native/social-sharing';
import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Calendar } from '@ionic-native/calendar';
import moment from 'moment';


@Component({
  selector: 'page-event-details',
  templateUrl: 'event-details.html',
})
export class EventDetailsPage {
  event: any;
  moment = moment;

  constructor(public navCtrl: NavController, public navParams: NavParams, private calendar: Calendar, private socialSharing: SocialSharing) {
    this.event = this.navParams.get('event');
  }

  addCalendar() {
    this.calendar.createEventInteractively(this.event.title, this.event.address, this.event.desc, this.event.startDate, this.event.endDate).then((status) => {
      console.log(status);
    }, (err) => {
      console.log(err);
    });
  }

  share() {
    this.socialSharing.share(this.event.desc, this.event.title, null, 'http://realmofglory.org/').then(data => {});
  }

}



